import React, { useState, useEffect } from 'react';
import '../styles/Variables.scss';
import '../styles/FindUs.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import {useCart} from "../context/CartContext";


const FindUs = () =>{
    const {isCheckout, thecart} = useCart()
    const [classesSocial, setClassesSocial] = useState("social-media-container")

    useEffect(() => {
        if(!isCheckout&&thecart.length>0){
            setClassesSocial("social-media-container padding-bottom")
        }else{
            setClassesSocial("social-media-container")
        }
    }, [thecart, isCheckout])
    
    return(
        <div className={classesSocial}>
            <div className="contact_text">Επικοινώνησε μαζί μας.</div>
            <div className="social-media">
                <i className="bi bi-facebook" style={{ fontSize: 30 }}></i>
                <i className="bi bi-messenger" style={{ fontSize: 30 }}></i>
                <i className="bi bi-instagram" style={{ fontSize: 30 }}></i>
                <i className="bi bi-telephone" style={{ fontSize: 30 }}></i>    
            </div>
        </div>
    )
}

export default FindUs;