import React, {useState, useEffect} from 'react';
import '../styles/Variables.scss';
import '../styles/Checkout.scss';
import ThankYouPage from './ThankYouPage'
import 'bootstrap-icons/font/bootstrap-icons.css';
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import {useCart} from "../context/CartContext";
import CheckoutValidation from './Validations/CheckoutValidations';

const Checkout = () =>{
    const {setOpenCart, billTotal, setIsCheckout, thecart} = useCart()
    const [deliveryOption, setDeliveryOption] = useState('');
    const [paymentOption, setPaymentOption] = useState('');
    const [info, setInfo] = useState({})
    const [errors, setErrors] = useState(null)
    const [thankyou, setThankyou] = useState(false)
    

    useEffect(() => {
      if(errors!=null){
        console.log(errors)
        var checkoutIsValid =true;
        var size = Object.keys(errors).length;
        for(var i=0;i<size;i++){
          if((errors[Object.keys(errors)[i]][0].hasError)){
            checkoutIsValid = false
          }
        }
        console.log("The form is valid? " + checkoutIsValid)
        if(checkoutIsValid){
          setThankyou(true)
          let checkoutDiv = document.getElementsByClassName('checkout-container')[0];
          checkoutDiv.classList.toggle("hidden");

          
      let brief = document.getElementsByClassName('order-brief-wrapper')[0];
      brief.classList.add("closed-brief");

        }
      }
    }, [errors])

    const leaveFromCheckout = () => {
      setIsCheckout(false)
      setOpenCart(false)
    }
    const toggleShowBrief = () =>{
      let brief = document.getElementsByClassName('order-brief-wrapper')[0];
      brief.classList.toggle("closed-brief");
    }
    const handleSubmit = () =>{
      let checkoutValidationsArray = CheckoutValidation()
      setErrors(checkoutValidationsArray)
      console.log("Info firstname: " + info.firstName)
      console.log("Info lastname: " + info.lastName)
      console.log("Info email: " + info.email)
      console.log("Info mobile: " + info.mobile)
      console.log("Info address: " + info.address)
      console.log("Info city: " + info.city)
      console.log("Info zip: " + info.zip)
      console.log("Info deliveryOption: " + info.deliveryOption)
      console.log("Info paymentOption: " + info.paymentOption)
      console.log("Info comments: " + info.comments)
    }
    return(
        <React.Fragment>
            <div className="checkout-wrapper">

            <div className="order-brief-wrapper">
              <div className="order-brief-title" onClick={toggleShowBrief}>Παραγγελία</div>
              <div className="order-brief-container">
              {thecart.map((item, index) =>{
                return (
                <div key={index}  className="checkout-item">
                  <div  className="checkout-item-posotita"><b>{item.posotita}</b></div>
                  <div  className="checkout-item-title">{item.title}</div>
                  <div  className="checkout-item-price"><b>{item.price}&euro;</b></div>
                </div>)
                })
              }
               <div  className="checkout-item last-total">
                  <div  className="checkout-item-posotita"></div>
                  <div  className="checkout-item-title">Σύνολο: </div>
                  <div  className="checkout-item-price"><b>{billTotal()}&euro;</b></div>
                </div>
              </div>
              </div>

                <div className="checkout-container">
                <div className="checkout-title">Ολοκληρωση Παραγγελίας</div>
                <form onSubmit={handleSubmit}>
                <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="firstName"
            name="firstName"
            label="Όνομα"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'firstName',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  firstName: e.target.value
                })
              }} 
              />
              {errors!=null&&errors.firstName[0].hasError&&<div className="error-msg">{errors.firstName[0].errorMsg}</div>}
          </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="lastName"
            name="lastName"
            label="Επώνυμο"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'lastName',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  lastName: e.target.value
                })
              }} 
               />
              {errors!=null&&errors.lastName[0].hasError&&<div className="error-msg">{errors.lastName[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="mobile"
            name="mobile"
            fullWidth
            label="Τηλέφωνο"
            variant="outlined"
            inputProps={{
              autoComplete: 'mobile',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  mobile: e.target.value
                })
              }} 
               />
              {errors!=null&&errors.mobile[0].hasError&&<div className="error-msg">{errors.mobile[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          required={true}
            id="email"
            name="email"
            label="Email"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'Email',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  email: e.target.value
                })
              }} 
               />
              {errors!=null&&errors.email[0].hasError&&<div className="error-msg">{errors.email[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
        <TextField
          required={true}
            id="address"
            name="address"
            label="Διεύθυνση"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'address',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  address: e.target.value
                })
              }} 
               />
              {errors!=null&&errors.address[0].hasError&&<div className="error-msg">{errors.address[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={6} sm={4}>
        <TextField
          required={true}
            id="city"
            name="city"
            label="Πόλη"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'city',
                form: {
                  autoComplete: 'off',
                },
              }}
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  city: e.target.value
                })
              }}  />
              {errors!=null&&errors.city[0].hasError&&<div className="error-msg">{errors.city[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={6} sm={2}>
        <TextField
          required={true}
            id="zip"
            name="zip"
            label="Zip"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'zip',
                form: {
                  autoComplete: 'off',
                },
                maxLength: 5 
              }} 
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  zip: e.target.value
                })
              }}
            />
              {errors!=null&&errors.zip[0].hasError&&<div className="error-msg">{errors.zip[0].errorMsg}</div>}
        </Grid>
        <Grid item xs={12} sm={6}>
        <FormControl variant="outlined" fullWidth sx={{ minWidth: 120 }}>
            <InputLabel id="fieldValue" 
          required={true}>Τρόπος Αποστολής</InputLabel>
            <Select
            id="deliveryOption"
            labelId="delivery-option"
            value={deliveryOption}
            
            onChange={(e) => {
              setDeliveryOption(e.target.value)
              setInfo({
                ...info,
                deliveryOption: e.target.value
              })
            }}
            label="Τρόπος Αποστολής"
            >
            <MenuItem value={"Αποστολή στο σπίτι"}>Αποστολή στο σπίτι</MenuItem>
            <MenuItem value={"Παραλαβή από το κατάστημα"}>Παραλαβή από το κατάστημα</MenuItem>
            </Select>
        </FormControl>
              {errors!=null&&errors.deliveryOption[0].hasError&&<div className="error-msg">{errors.deliveryOption[0].errorMsg}</div>}
        </Grid><Grid item xs={12} sm={6}>
        <FormControl variant="outlined" fullWidth sx={{ minWidth: 120 }}>
            <InputLabel id="payment-option"
          required={true}>Τρόπος Πληρωμής</InputLabel>
            <Select
            labelId="payment-option"
            id="paymentOption"
            value={paymentOption}
            
            onChange={(e) => {              
              setPaymentOption(e.target.value)
              setInfo({
                ...info,
                paymentOption: e.target.value
              })
            }}
            label="Τρόπος Πληρωμής"
            >
            <MenuItem value={"Μετρητά"}>Μετρητά</MenuItem>
            <MenuItem value={"Με κάρτα"}>Με κάρτα</MenuItem>
            </Select>
              {errors!=null&&errors.paymentOption[0].hasError&&<div className="error-msg">{errors.paymentOption[0].errorMsg}</div>}
        </FormControl>
        </Grid>
      <Grid item xs={12}>
        <TextField
            id="comments"
            name="comments"
            label="Σχόλια"
            fullWidth
            variant="outlined"
            inputProps={{
              autoComplete: 'comments',
                form: {
                  autoComplete: 'off',
                },
              }} 
            
              onChange={(e) => {
                //let tempValue = e.target.id
                setInfo({
                  ...info,
                  comments: e.target.value
                })
              }}
               />
        </Grid>
      </Grid>
      <div className="checkout-button-container">
        <Button variant="contained" style={{backgroundColor: '#efefef', color: '#000', margin: '10px 0 0', width: '40%', padding: '10px', boxShadow: '0 0'}} onClick={leaveFromCheckout}>Επιστροφη</Button>
        <Button variant="contained" onClick={handleSubmit} style={{backgroundColor: '#9cc094', color: '#fff', margin: '10px 0 0', width: '40%', padding: '10px', boxShadow: '0 0'}}>Ολοκληρωση</Button>
      </div>    
      </form>
                </div>
            </div>
            {thankyou && <ThankYouPage info={info} />}
        </React.Fragment>
    )
}

export default Checkout;