import React from 'react';
import '../styles/Variables.scss';
import '../styles/MiniCart.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import Button from '@mui/material/Button';
import {useCart} from "../context/CartContext";


const MiniCart = ({content, clear}) => {
  const {setOpenCart, openCart, billTotal, goToCheckout} = useCart()
  const closeCartWindow = () => {
    setOpenCart(!openCart)
    let body = document.getElementsByTagName('body')[0];
    body.classList.toggle("opened-cart");
  }

  let body = document.getElementsByTagName('body')[0];
  body.classList.add("opened-cart");
    if(content.length>0){
      return (
        <div className="cart-outer">
          <div className="background-cart" onClick={closeCartWindow}></div>
        <div className="cart-wrapper">
        <div className="cart-container">
          <div className="header-mini-cart">
          <div className="title-cart">Καλάθι</div>
          <div className="close-cart" onClick={closeCartWindow}><i className="bi bi-x-square"></i></div>

          </div>
          <div className="mini-cart-items-container">
          {content.map((item, index) =>{
                return (
                <div key={index}  className="mini-cart-item">
                  <div  className="mini-cart-item-posotita"><b>{item.posotita}</b></div>
                  <div  className="mini-cart-item-title">{item.title}</div>
                  <div  className="mini-cart-item-price"><b>{item.price}&euro;</b></div>
                </div>)
              })
          }
          </div>
          <div>
            <div className="mini-cart-total">Σύνολο: <b>{billTotal()}&euro;</b></div>
          </div>
          <Button variant="contained" style={{backgroundColor: '#9cc094', color: '#fff', margin: '10px 0', width: '100%', padding: '10px', boxShadow: '0 0'}} onClick={goToCheckout}>Ολοκληρωση</Button>
            <div className="clear-cart" onClick={clear}>Καθαρισμός Καλαθιού <i className="bi bi-trash"></i></div>
        </div>
      </div>
      </div>
      )
    }
    return null
  }

export default MiniCart;