import React from 'react';
import '../styles/Variables.scss';
import '../styles/Header.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import {useCart} from "../context/CartContext";



const Header = () =>{
    const {thecart, setOpenCart, openCart, billTotal, isCheckout} = useCart()
    let totalNumberOfProduct = () => {
        let sumProd = 0 ;
        for (let i = 0; i < thecart.length; i++) {
            sumProd = sumProd + thecart[i].posotita
        }
        return sumProd
    }
    const toogleCart = () => {
        setOpenCart(!openCart)
    }
    return(
        <div className="header-wrapper">
        <div className="header-container">
            <div className="header-title">
                <div className="header-logo">DEMO</div>
                <div className="header-description">Local Products</div>    
                <div className="container header-cart">
                {!isCheckout&&
                    (thecart<1 ? 
                    (<div className="my-cart-on-header"><span className="my-cart-text">Άδειο Καλάθι</span><i className="bi bi-cart-x"></i></div>) 
                   :
                   (<div className="my-cart-on-header" onClick={toogleCart}><span className="my-cart-text">Σύνολο:</span><span className="my-cart-bill-total">{billTotal()}&euro;</span><i className="bi bi-cart3"></i><span className='badge badge-warning'>{totalNumberOfProduct()}</span></div>) 
                    )
                }
                </div>
            </div>       
        </div>
        </div>
    )
}

export default Header;