import React, {useState, useEffect} from 'react';
import '../styles/Variables.scss';
import '../styles/AllProducts.scss';
import schema from './schema/schema'
import Product from './Product'
import MiniCart from './MiniCart'
import Checkout from './Checkout.jsx'
import {useCart} from "../context/CartContext";
import FixedButton from './FixedButton';

let choosen_posotita = 1;

const AllProducts = () => {  
  
  const {setThecart, openCart, setOpenCart, isCheckout} = useCart()
  const [products, setProducts] = useState(schema)
  const [cart, setCart] = useState([])
  const initProduct = () => {
    const tempProd = schema
    for (let i = 0; i < tempProd.length; i++) {
      for (let j = 0; j < tempProd[i].items.length; j++) {
          tempProd[i].items[j].posotita = 0;
      }   
    }
    setProducts(tempProd)
  }

  useEffect(() => {
    initProduct();
  }, [])

  var choosen_id = ""
  var choosen_line = ""


  useEffect(()=>{
    let cartArr =[]
    for (let i = 0; i < products.length; i++) {
      for (let j = 0; j < products[i].items.length; j++) {
        if(products[i].items[j].posotita>0){
          //console.log(products[i].items[j])
          cartArr.push(products[i].items[j])
          
        }
      }   
    }
    setCart(cartArr)
  },[products])


  useEffect(()=>{
    cart.length && console.log(cart)
    setThecart(cart)
  },[cart, setThecart])
  
  const AddProduct = (data) => {    
    for (let i = 0; i < products.length; i++) {
      const list = products[i].items;
      list.map((itemid) =>{
        if(itemid.id === data.id){
          choosen_id = data.id;
          choosen_line = i
        }
       return choosen_id
      })
    }
    const newProds = [...products];
    newProds[choosen_line].items.filter(todoId => todoId.id === choosen_id)[0].posotita =1
    setProducts(newProds);
  }

const handlePlus = (data) =>{
  for (let i = 0; i < products.length; i++) {
    const list = products[i].items;
    list.map((itemid, index) =>{
      if(itemid.id === data.id){
        choosen_id = data.id;
        choosen_posotita = data.posotita + 1
        choosen_line = i
      }
      return choosen_id
     
    })
  }
  const newProds = [...products];
  newProds[choosen_line].items.filter(todoId => todoId.id === choosen_id)[0].posotita = choosen_posotita
  setProducts(newProds);
}
const handleMinus = (data) =>{
  for (let i = 0; i < products.length; i++) {
    const list = products[i].items;
    list.map((itemid) =>{
      if(itemid.id === data.id){
        choosen_id = data.id;
        choosen_posotita = data.posotita - 1
        choosen_line = i
      }
     return choosen_id
    })
  }
  const newProds = [...products];
  newProds[choosen_line].items.filter(todoId => todoId.id === choosen_id)[0].posotita = choosen_posotita
  setProducts(newProds);
}
const clearCart = () =>{
  initProduct()
  setCart([]);
  setOpenCart(false)
  let body = document.getElementsByTagName('body')[0];
  body.classList.remove("opened-cart");
}
const toogleHiddenClass = (element) => {
  element.target.closest(".category_outer").classList.toggle("hidden_category")
 }
  return (
    <div className="content">

      {!isCheckout&&products.map(function({categoryId, name, items, hidden},index) {              
              return(
                  <div className={!hidden ? "category_outer" : "category_outer hidden_category"} key={index}>
                  <div className="category_name" onClick={((e) => toogleHiddenClass(e))}>{name}</div>
                    <div className="categorys_products">
                      {items.map((item)=>{
                        return (<Product data={item} key={item.id} handleClick={AddProduct} handleMinus={handleMinus} handlePlus={handlePlus}/>)
                      })}
                      </div>
                  </div>
            )
          })
        }
        {(cart.length>0&&openCart&&!isCheckout) && <MiniCart content={cart} clear={clearCart}/>}
        {isCheckout&&<Checkout />}
        {(cart.length>0&&!isCheckout) && <FixedButton/>}
</div>
  );
}

export default AllProducts;