import React from 'react';
import '../styles/Variables.scss';
import '../styles/ThankYouPage.scss';


const ThankYouPage = ({info}) =>{
    
    return(
<div className="thankyou-wrapper">
    <div className="thankyou-container">
        <div className="thankyou-title">
        Σε ευχαριστούμε, η παραγγελία σου έχει καταχωρυθεί με επιτυχία.
        </div>
        
        <div className="thankyou-content">
            <div className="thankyou-content-title">
            Στοιχεία παραγγελίας
            </div>
            
            <div className="thankyou-info-container">
                <div className="thankyou-info-full-name thank-you-sides">
                    <span className="thankyou-left">Ονοματεπώνυμο: </span>
                    <span className="thankyou-right">{info.firstName} {info.lastName}</span>
                </div>
                <div className="thankyou-info-contact thank-you-sides">
                    <span className="thankyou-left">Στοιχεία επικοινωνίας: </span>
                    <span className="thankyou-right">{info.mobile}, {info.email} </span>
                </div>
                <div className="thankyou-info-full-address thank-you-sides">
                    <span className="thankyou-left">Διεύθυνση: </span>
                    <span className="thankyou-right">{info.city}, {info.address}. {info.zip} </span>
                </div>
                <div className="thankyou-info-details thank-you-sides">
                <span className="thankyou-left">Τρόπος αποστολής: </span>
                    <span className="thankyou-right">{info.deliveryOption} </span>
                </div>
                <div className="thankyou-info-details thank-you-sides">
                    <span className="thankyou-left">Τρόπος πληρωμής: </span>
                    <span className="thankyou-right">{info.paymentOption} </span>
                </div>
                <div className="thankyou-comments thank-you-sides">
                <span className="thankyou-left">Σχόλια: </span>
                    <span className="thankyou-right">{info.comment} </span>
                </div>
            </div>
        </div>
    </div>
</div>
    )
}

export default ThankYouPage;